@import url("https://fonts.googleapis.com/css2?family=Roboto+Mono:ital,wght@1,200&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Roboto+Condensed:wght@300&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Caveat&display=swap");

* {
  margin: 0;
  padding: 0;
}

body {
  width: 100vw;
  height: 100vh;
  display: flex;

  flex-direction: column;
  font-family: "Roboto Condensed", sans-serif;
}

#container {
  height: 100vh;
  width: 100vw;
  position: fixed;
  z-index: -1;
  top: 0;
  left: 0;
}
main {
  display: flex;
  height: 80vh;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.main {
  color: white;
  font-family: "Caveat", cursive;
  transition: 0.3s ease-in-out;
}

.main:hover {
  color: black;
}
footer {
  font-size: 1.5rem;
  text-align: center;
  display: flex;
  padding: 10px;
  justify-content: left;
  margin-top: 25px;
  color: white;
}
h2 {
  font-size: 6rem;
  letter-spacing: 5px;
}
nav {
  color: white;
  font-size: 2rem;
  padding: 15px;
}
i {
  padding: 5px;
  color: white;
}

.social:hover {
  font-size: 1.5rem;
}
h4 {
  letter-spacing: 3px;
}
p {
  color: white;
  margin-left: 10px;
  padding: 15px;
  font-size: 1.2rem;
  letter-spacing: 3px;
}

.social {
  display: flex;
  width: 100%;
  margin-right: 40px;
  justify-content: flex-end;
  transition: 0.3s ease-in-out;
}
.social p {
  padding: 10px;
}

::-webkit-scrollbar {
  display: none;
}
